.title{
    margin: 1rem;
    color: #eee;
    font-size: 2rem;
    text-align: center;
}

.choseGift{
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    border-bottom: .0615rem solid rgba(255, 255, 255, .3);
    margin-right: 10px;
    padding: 10px 0;
}

.choseGift div{
    color: #bbb;
    padding: 10px;
    transition: all .2s;
    cursor: pointer;
    box-sizing: border-box;
}

.choseGift div:hover{
    color: #eee;
    background-color: rgba(0, 0, 0, 0.3);
    padding: 10px;
}

.choseGift div.active {
    border: 0.0615rem solid rgba(255, 255, 255, .3);
    background-color: #2c06ff;
}

.choseGift div.active:hover{
    border: 0.0615rem solid rgba(255, 255, 255, .3);
    background-color: #2c06ff;
}

.valor{
    display: flex;
    justify-content: space-between;
    max-width: 300px;
    margin: 1rem auto;
    color: #eee;
}

.valor input{
    background: none;
    outline: none;
    border: .0615rem solid rgba(255, 255, 255, .3);
    color: #bbb;
}

.input{
    height: 500px;
    width: 50%;
    margin: auto;
    text-align: center;
    resize: none;
    display: block;
    margin: auto;
    background: none;
    outline: none;
    color: #eeed;
    font-size: 14px;
}

.btn{
    display: block;
    margin: 1rem auto;
    padding: .5rem 1rem;
    background: #2c06ff;
    border: none;
    color: #eee;
    transition: all .5s;
}

.btn:hover{
    cursor: pointer;
    transform: scale(1.1);
}