.container{
    /* max-width: 500px; */
    width: 100%;
    background: #1f2251;
    display: flex;
    height: 100vh;
    justify-content: center;
    align-items: center;
}

.content{
    width: 100%;
    max-width: 500px;
    box-shadow: 0px 0px 4px 1px rgba(0, 0, 0, 0.2);
    padding: 1rem 10px;
    border-radius: 10px;
    background: rgba(0, 0, 0, 0.3);
    display: flex;
    flex-direction: column;
    align-items: center;
}

.err {
    color: #eee;
    background: rgba(255, 0, 0, 0.678);
    padding: .5rem 2rem;
    border-radius: 10px;
}