.container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    background-color: #1f2251;
}

.content {
    box-shadow: 0px 0px 4px 1px rgba(0, 0, 0, 0.4);
    border-radius: 10px;
    transition: all .2s;
}

.content a {
    color: #eee;
    transition: all .2s;
    text-decoration: none;
    padding: 1rem 3rem;    
    display: block;
}

.content:hover{
    cursor: pointer;
    background-color: #ba54f5;
}