.container{
    width: 150px;
    height: 100vh;
    background-color: #2c06ff;
    box-shadow: 0px 0px 4px 1px rgba(0, 0, 0, 0.5);
    padding: 1rem 0;
    position: fixed;
    top:  0px;
    left: 0px;
}

.btns{
    position: absolute;
    right: 0px;
    bottom: 0px;
    transform: translateY(-50%);
    display: flex;
    width: 150px;
}

.btns div {
    color: #eee;
    background-color: #e11;
    width: 100%;
    padding: 1rem;
    cursor: pointer;
}

.title{
    color: #eee;
    font-size: 1.4rem;
    font-weight: 500;
    text-align: center;
    margin-bottom: 1rem;
}